import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { renderField } from "../../renderField";

class FormPembayaranHutangSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              component={renderField}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Supplier</label>
            <Field name="supplier" className="form-control" component="select">
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-lg-4 col-md-4">
            <Field
              name="noBon"
              component={renderField}
              type="text"
              label="No Bon Bayar"
            />
          </div>
          <div className="col-md-6 col-lg-6">
            <Field
              name="detail"
              label="Detail"
              type="text"
              component={renderField}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormPembayaranHutangSupplier = reduxForm({
  form: "FormPembayaranHutangSupplier",
  enableReinitialize: true,
})(FormPembayaranHutangSupplier);
export default connect()(FormPembayaranHutangSupplier);
